/* UNIVERSAL */

* {
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    color: #FF00C7;
    margin: 0;
}

body {
    background-color: #020050;
}

a {
    text-decoration: none;
    color: inherit;
}

.header {
    background-color: #0AD2ED;
    padding-bottom: 10px;
    border: 1px solid #FF00C7;
    text-align: center;
}

.header h1 {
    font-weight: 500;
}

.author {
    font-family: 'Source Code Pro', monospace;
    color: #1B03A3;
}


/* BLOG LIST */

.blog-list-page {
    padding: 20px;
    text-align: left;
    max-width: 700px;
    margin: 0 auto;
}

.intro {
    margin-bottom: 30px;
}

.intro h2 {
    margin: 15px 0 20px;
}

.intro p {
    color:rgba(77 160 229);
    font-weight: 500;
    margin-bottom: 10px;
}

.intro a, .contact a {
    color: darkorange;
    text-decoration: none;
    transition: background-color 0.5s;
}

.intro a:hover, .contact a:hover {
    background-color: #057f8f;
    border-radius: 3px;
}

.blog-list-page h3 {
    margin-bottom: 10px;
    font-size: 1.4rem;
}

.blog-list li {
    list-style-type: none;
    padding: 10px 5px;
    font-weight: 700;
    font-size: 1.2rem;
    font-family: 'Source Code Pro', monospace;
    color:rgba(77 160 229);
}

.blog-list button {
    padding: 10px;
    margin-bottom: 20px;
    background-color: #0AD2ED;
    border-radius: 20px;
    border: 2px solid #FF00C7;
    color: #FF00C7;
    font-weight: 700;
    transition: All 250ms ease;
    box-shadow: 0px 3px 0px #545353;
    position: relative;
}

.blog-list button:active {
    box-shadow: none !important;
    transition: All 250ms ease;
    position: relative;
    top: 3px;

}

/* BLOG POST */

.blog-post {
    text-align: left;
    padding:  35px 15px;
    margin: 0 auto;
    max-width: 700px;
}

.blog-post h3 {
    margin-bottom: 25px;
    font-size: 1.4rem;
}

.date {
    margin-bottom: 20px;
}

.date-slice {
    color: rgba(77 160 229);
}

p.contact {
    color:rgba(77 160 229);
    font-weight: 500;
    margin-bottom: 40px;
    font-style: italic;
}

.content {
    margin-bottom: 40px;
}

.content p{
    color:rgba(77 160 229);
    font-weight: 600;
    font-size: 1.2rem;
    font-family: 'Source Code Pro', monospace;
    margin-bottom: 25px;
}

.content a {
    color: darkorange;
    text-decoration: none;
    font-family: 'Source Code Pro', monospace;
    transition: background-color 0.5s;
}

.content a:hover {
    background-color: #057f8f;
    border-radius: 3px;
}

pre {
    font-family: 'Source Code Pro', monospace;
    color:chartreuse;
}

.blog-post button {
    padding: 10px;
    margin-bottom: 20px;
    background-color: #0AD2ED;
    border-radius: 20px;
    border: 2px solid #FF00C7;
    color: #FF00C7;
    font-weight: 700;
    display: block;
    margin: 0 auto;
    transition: All 250ms ease;
    box-shadow: 0px 3px 0px #545353;
    position: relative;
}

.blog-post button:active {
    box-shadow: none !important;
    transition: All 250ms ease;
    position: relative;
    top: 3px;
}

/* CONTACT PAGE */

.contact-page {
    padding: 40px;
    max-width: 700px;
    margin: 0 auto;
}

.contact-form {
    margin: 0 auto;
    background-color: #0AD2ED;
    border-radius: 5px;
    padding: 20px;
    max-width: 500px;
}

.contact-form h2 {
    text-align: center;
    margin-bottom: 15px;
}

.contact-form input, textarea {
    display: block;
    margin: 0 auto 10px;
    border: none;
    border-radius: 5px;
    padding: 5px;
    color: gray;
}

.contact-form button {
    padding: 10px;
    background-color: #020050;
    border-radius: 20px;
    border: 2px solid #FF00C7;
    color: #FF00C7;
    font-weight: 700;
    display: block;
    margin: 0 auto;
    transition: All 250ms ease;
    box-shadow: 0px 3px 0px #545353;
    position: relative;
}

.contact-form button:active {
    box-shadow: none !important;
    transition: All 250ms ease;
    position: relative;
    top: 3px;
}

.contact-form p {
    text-align: center;
    margin-top: 20px;
}
